},
onHide () {
console.log('on hide')
},
onShow () {
console.log('on show')
},
showPlugin () {
this.show = true;
},
showPlugin2 () {
this.show2 = true;
},
showPosition () {
this.showPositionValue = true
},
day1 () {
var _this = this;
setInterval(function(){
var time=Date.parse(new Date());
var lasttime=Date.parse("2016-03-28");
var day=parseInt((time-lasttime)/(1000*60*60*24));
_this.loveDay = day;
},1000)
},
day2 () {
var _this = this;
setInterval(function(){
var time=Date.parse(new Date());
var lasttime=Date.parse("2017-11-26");
var day=parseInt((lasttime-time)/(1000*60*60*24));
_this.babyDay = day;
},1000)
},
day3 () {
var _this = this;
setInterval(function(){
var time=Date.parse(new Date());
var lasttime=Date.parse("2017-12-09");
var day=parseInt((lasttime-time)/(1000*60*60*24));
_this.myDay = day;
},1000)
},
day4 () {
var _this = this;
setInterval(function(){
var time=Date.parse(new Date());
var lasttime=Date.parse("2017-06-01");
var day=parseInt((time-lasttime)/(1000*60*60*24));
_this.betweenDay = day;
},1000)
}
},
data () {
return {
loveDay: '',
babyDay: '',
myDay: '',
betweenDay: '',
showPositionValue: false,
demo06_list: urlList,
images: [
